.company__cta {
    border: 1px solid;

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.company__cta-content {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;

    color: #0e0e0e;
}

.company__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;

    color: #000000;
}

.company__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.company__cta-btn button {
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;

    background: #000000;
    border-radius: 40px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
    
    color: #ffffff;

    transition: 0.1s;
}

.company__cta-btn button:hover {
    opacity: 0.9;
}

@media screen and (max-width:650px) {
    .company__cta {
        flex-direction: column;
    }

    .company__cta-btn {
        margin: 2rem 0 0;
    }
 }

 @media screen and (max-width:550px) {
    .company__cta {
        margin: 4rem 2rem;
    }

    .company__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .company__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
 }